// ÂÆö‰πâÊìç‰ΩúÁ±ªÂûã
export interface TodoAction {
  action: 'add' | 'complete' | 'delete' | 'list' | 'clear_completed' | 'clear_all';
  task?: string;
  response?: string;
}

// ÂÆâÂÖ®Ëß£ÊûêJSONÁöÑÂáΩÊï∞
export function safeParseJSON(text: string): TodoAction | null {
  try {
    // È¶ñÂÖàÂ∞ùËØïÊèêÂèñmarkdown‰ª£Á†ÅÂùó‰∏≠ÁöÑJSON
    const codeBlockMatch = text.match(/```json\s*\n([\s\S]*?)\n```/);
    let jsonString = '';
    
    if (codeBlockMatch) {
      jsonString = codeBlockMatch[1].trim();
    } else {
      // Â¶ÇÊûúÊ≤°Êúâ‰ª£Á†ÅÂùóÔºåÂ∞ùËØïÁõ¥Êé•ÊèêÂèñJSON
      const jsonMatch = text.match(/\{[\s\S]*?\}/);
      if (!jsonMatch) {
        return null;
      }
      jsonString = jsonMatch[0];
    }

    console.log('ÊèêÂèñÁöÑJSONÂ≠óÁ¨¶‰∏≤:', jsonString);
    
    const parsed = JSON.parse(jsonString);
    
    // È™åËØÅËß£ÊûêÁªìÊûúÁöÑÁªìÊûÑ
    if (typeof parsed === 'object' && 
        parsed !== null && 
        'action' in parsed &&
        ['add', 'complete', 'delete', 'list', 'clear_completed', 'clear_all'].includes(parsed.action)) {
      
      // ÂØπ‰∫éÈúÄË¶ÅtaskÁöÑÊìç‰ΩúÔºåÈ™åËØÅtaskÂ≠óÊÆµ
      if ((parsed.action === 'add' || parsed.action === 'complete' || parsed.action === 'delete') && 
          (!parsed.task || typeof parsed.task !== 'string' || parsed.task.trim() === '')) {
        console.warn('Êìç‰ΩúÈúÄË¶ÅtaskÂ≠óÊÆµ‰ΩÜÊú™Êèê‰æõÊàñ‰∏∫Á©∫');
        return null;
      }
      
      return {
        action: parsed.action,
        task: parsed.task?.trim(),
        response: parsed.response
      };
    }
    
    console.warn('JSONÁªìÊûÑÈ™åËØÅÂ§±Ë¥•:', parsed);
    return null;
  } catch (error) {
    console.warn('JSONËß£ÊûêÂ§±Ë¥•:', error);
    return null;
  }
}

// Ê£ÄÊü•Ê∂àÊÅØÊòØÂê¶ÂåÖÂê´JSONÊìç‰Ωú
export function hasActionInMessage(message: string): boolean {
  // Ê£ÄÊü•ÊòØÂê¶ÂåÖÂê´markdown‰ª£Á†ÅÂùóÊàñÁõ¥Êé•ÁöÑJSON
  return /```json[\s\S]*?```/.test(message) || /\{[\s\S]*?"action"[\s\S]*?\}/.test(message);
} 