import { useState, useCallback } from 'react';

// ËÆ∞ÂøÜÊù°ÁõÆÊé•Âè£
export interface MemoryEntry {
  id: string;
  timestamp: number;
  type: 'user_message' | 'ai_response' | 'action_executed' | 'context_reference';
  content: string;
  metadata?: {
    action?: string;
    taskId?: number;
    taskText?: string;
    reference?: string;
  };
}

// ‰∏ä‰∏ãÊñáÁ±ªÂûã
export interface Context {
  recentActions: Array<{
    action: string;
    taskId?: number;
    taskText?: string;
    timestamp: number;
  }>;
  lastAddedTask?: {
    id: number;
    text: string;
    timestamp: number;
  };
  recentMessages: Array<{
    content: string;
    timestamp: number;
    type: 'user' | 'ai';
  }>;
}

export function useMemory() {
  const [memory, setMemory] = useState<MemoryEntry[]>([]);
  const [context, setContext] = useState<Context>({
    recentActions: [],
    recentMessages: []
  });

  // Ê∑ªÂä†ËÆ∞ÂøÜÊù°ÁõÆ
  const addMemoryEntry = useCallback((entry: Omit<MemoryEntry, 'id' | 'timestamp'>) => {
    const newEntry: MemoryEntry = {
      ...entry,
      id: `mem_${Date.now()}_${Math.random().toString(36).substr(2, 9)}`,
      timestamp: Date.now()
    };
    
    setMemory(prev => [...prev.slice(-49), newEntry]); // ‰øùÊåÅÊúÄËøë50Êù°ËÆ∞ÂΩï
    return newEntry;
  }, []);

  // ËÆ∞ÂΩïÁî®Êà∑Ê∂àÊÅØ
  const recordUserMessage = useCallback((message: string) => {
    addMemoryEntry({
      type: 'user_message',
      content: message
    });

    // Êõ¥Êñ∞‰∏ä‰∏ãÊñá‰∏≠ÁöÑÊúÄËøëÊ∂àÊÅØ
    setContext(prev => ({
      ...prev,
      recentMessages: [
        ...prev.recentMessages.slice(-9), // ‰øùÊåÅÊúÄËøë10Êù°Ê∂àÊÅØ
        {
          content: message,
          timestamp: Date.now(),
          type: 'user'
        }
      ]
    }));
  }, [addMemoryEntry]);

  // ËÆ∞ÂΩïAIÂõûÂ§ç
  const recordAIResponse = useCallback((response: string) => {
    addMemoryEntry({
      type: 'ai_response',
      content: response
    });

    // Êõ¥Êñ∞‰∏ä‰∏ãÊñá‰∏≠ÁöÑÊúÄËøëÊ∂àÊÅØ
    setContext(prev => ({
      ...prev,
      recentMessages: [
        ...prev.recentMessages.slice(-9),
        {
          content: response,
          timestamp: Date.now(),
          type: 'ai'
        }
      ]
    }));
  }, [addMemoryEntry]);

  // ËÆ∞ÂΩïÊâßË°åÁöÑÂä®‰Ωú
  const recordAction = useCallback((action: string, taskId?: number, taskText?: string) => {
    addMemoryEntry({
      type: 'action_executed',
      content: `ÊâßË°åÂä®‰Ωú: ${action}${taskText ? ` - ${taskText}` : ''}`,
      metadata: {
        action,
        taskId,
        taskText
      }
    });

    // Êõ¥Êñ∞‰∏ä‰∏ãÊñá‰∏≠ÁöÑÊúÄËøëÂä®‰Ωú
    setContext(prev => ({
      ...prev,
      recentActions: [
        ...prev.recentActions.slice(-9), // ‰øùÊåÅÊúÄËøë10‰∏™Âä®‰Ωú
        {
          action,
          taskId,
          taskText,
          timestamp: Date.now()
        }
      ],
      // Â¶ÇÊûúÊòØÊ∑ªÂä†‰ªªÂä°ÔºåÊõ¥Êñ∞ÊúÄÂêéÊ∑ªÂä†ÁöÑ‰ªªÂä°
      ...(action === 'add' && taskId && taskText ? {
        lastAddedTask: {
          id: taskId,
          text: taskText,
          timestamp: Date.now()
        }
      } : {})
    }));
  }, [addMemoryEntry]);

  // ËÆ∞ÂΩï‰∏ä‰∏ãÊñáÂºïÁî®
  const recordContextReference = useCallback((reference: string, content: string) => {
    addMemoryEntry({
      type: 'context_reference',
      content,
      metadata: {
        reference
      }
    });
  }, [addMemoryEntry]);

  // Ëß£Êûê‰∏ä‰∏ãÊñáÂºïÁî®ÔºàÂ¶Ç"ÂàöÊâçÈÇ£‰∏™"„ÄÅ"ÊúÄÂêé‰∏Ä‰∏™"Á≠âÔºâ
  const resolveContextReference = useCallback((userInput: string) => {
    const input = userInput.toLowerCase();
    let resolvedContext = '';
    
    // Ê£ÄÊü•ÊòØÂê¶ÂºïÁî®‰∫Ü"ÂàöÊâç"„ÄÅ"ÊúÄÂêé"„ÄÅ"‰∏ä‰∏Ä‰∏™"Á≠â
    if (input.includes('ÂàöÊâç') || input.includes('ÊúÄÂêé') || input.includes('‰∏ä‰∏Ä‰∏™') || input.includes('‰∏ä‰∏™')) {
      if (input.includes('Ê∑ªÂä†') || input.includes('‰ªªÂä°')) {
        // ÂºïÁî®ÊúÄÂêéÊ∑ªÂä†ÁöÑ‰ªªÂä°
        if (context.lastAddedTask) {
          resolvedContext = `ÂºïÁî®ÊúÄÂêéÊ∑ªÂä†ÁöÑ‰ªªÂä°: "${context.lastAddedTask.text}" (ID: ${context.lastAddedTask.id})`;
          recordContextReference('last_added_task', resolvedContext);
          return {
            type: 'task_reference',
            taskId: context.lastAddedTask.id,
            taskText: context.lastAddedTask.text,
            resolvedText: resolvedContext
          };
        }
      } else {
        // ÂºïÁî®ÊúÄÂêéÁöÑÂä®‰Ωú
        const lastAction = context.recentActions[context.recentActions.length - 1];
        if (lastAction) {
          resolvedContext = `ÂºïÁî®ÊúÄÂêéÁöÑÂä®‰Ωú: ${lastAction.action}${lastAction.taskText ? ` - ${lastAction.taskText}` : ''}`;
          recordContextReference('last_action', resolvedContext);
          return {
            type: 'action_reference',
            action: lastAction.action,
            taskId: lastAction.taskId,
            taskText: lastAction.taskText,
            resolvedText: resolvedContext
          };
        }
      }
    }

    // Ê£ÄÊü•ÊòØÂê¶ÂºïÁî®‰∫Ü"ÂÜç"„ÄÅ"ÂÜçÂä†"Á≠â
    if (input.includes('ÂÜçÂä†') || input.includes('ÂÜçÊ∑ªÂä†')) {
      const lastAddAction = context.recentActions
        .slice()
        .reverse()
        .find(action => action.action === 'add');
      
      if (lastAddAction) {
        resolvedContext = `Âü∫‰∫é‰∏äÊ¨°Ê∑ªÂä†ÁöÑ‰ªªÂä°Ê®°Âºè: "${lastAddAction.taskText}"`;
        recordContextReference('repeat_add_pattern', resolvedContext);
        return {
          type: 'repeat_pattern',
          action: 'add',
          referenceTask: lastAddAction.taskText,
          resolvedText: resolvedContext
        };
      }
    }

    return null;
  }, [context, recordContextReference]);

  // Ëé∑Âèñ‰∏ä‰∏ãÊñáÊëòË¶ÅÔºàÁî®‰∫éÂèëÈÄÅÁªôAIÔºâ
  const getContextSummary = useCallback(() => {
    const recentMessages = context.recentMessages.slice(-6); // ÊúÄËøë3ËΩÆÂØπËØù
    const recentActions = context.recentActions.slice(-5); // ÊúÄËøë5‰∏™Âä®‰Ωú
    
    let summary = '';
    
    if (recentMessages.length > 0) {
      summary += 'ÊúÄËøëÁöÑÂØπËØù:\n';
      recentMessages.forEach(msg => {
        summary += `${msg.type === 'user' ? 'Áî®Êà∑' : 'AI'}: ${msg.content}\n`;
      });
      summary += '\n';
    }
    
    if (recentActions.length > 0) {
      summary += 'ÊúÄËøëÁöÑÊìç‰Ωú:\n';
      recentActions.forEach(action => {
        summary += `${action.action}: ${action.taskText || 'Êó†ÂÖ∑‰Ωì‰ªªÂä°'}\n`;
      });
      summary += '\n';
    }
    
    if (context.lastAddedTask) {
      summary += `ÊúÄÂêéÊ∑ªÂä†ÁöÑ‰ªªÂä°: "${context.lastAddedTask.text}" (ID: ${context.lastAddedTask.id})\n`;
    }
    
    return summary;
  }, [context]);

  // Ê∏ÖÈô§ËÆ∞ÂøÜÔºàÂèØÈÄâÂäüËÉΩÔºâ
  const clearMemory = useCallback(() => {
    setMemory([]);
    setContext({
      recentActions: [],
      recentMessages: []
    });
  }, []);

  return {
    memory,
    context,
    addMemoryEntry,
    recordUserMessage,
    recordAIResponse,
    recordAction,
    recordContextReference,
    resolveContextReference,
    getContextSummary,
    clearMemory
  };
} 